<template>
  <el-table
    :data="tableData"
    border
    stripe 
    class="w24">
    <el-table-column
      prop="inventoryId"
      label="入库单号"
      width="180"
      align="center">
    </el-table-column>
    <el-table-column
      prop="systemMaterialId"
      label="系统物料号"
      width="180"
      align="center">
    </el-table-column>
    <el-table-column
      prop="scmMaterialId"
      label="SCM物料号"
      align="center">
    </el-table-column>
    <el-table-column
      prop="material"
      label="物料"
      align="center">
    </el-table-column>
    <el-table-column
      prop="materialGroup"
      label="物料组"
      align="center">
    </el-table-column>
    <el-table-column
      prop="inventoryNum"
      label="入库数量"
      align="center">
    </el-table-column>
    <el-table-column
      prop="inventoryTotalPrice"
      label="入库总价"
      align="center">
    </el-table-column>
    <el-table-column
      prop="inventoryPeople"
      label="入库人"
      align="center">
    </el-table-column>
    <el-table-column
      prop="inventoryTime"
      label="入库时间"
      align="center">
    </el-table-column>
  </el-table>
</template>

<script>
export default {
    props: ['tableData']
}
</script>



