<template>
    <el-card>
        <el-row class="mb15">
            入库日期：
            <el-date-picker
                v-model="value2"
                align="right"
                type="date"
                placeholder="选择日期"
                :picker-options="pickerOptions1"
                @change="search">
            </el-date-picker>
        </el-row>
        <el-row>
            <el-tabs type="border-card" @tab-click="handleClick">  
                <el-tab-pane label="已关联记录" >
                  <recordTable :tableData="tableData" />
                </el-tab-pane>
                <el-tab-pane label="未关联记录" >
                  <recordTable :tableData="tableData" />
                </el-tab-pane>
            </el-tabs>
        </el-row>
    </el-card>
</template>

<script>

import RecordTable from "../components/Table";
  export default {
    components:{
        RecordTable
    },
    data() {
      return {
        tableData:[],
        pickerOptions1: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          },
          shortcuts: [{
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '一周前',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        value2: '',
      };
    },
    methods: {
      //根据日期查找数据
      search() {
        this.getrecord0()
      },
      //获取已关联记录的数据
      async getrecord1(){
        return this.tableData = await this.$get('../../../../static/record1.json')
      },
      //获取为关联记录的数据
      async getrecord0(){
        return this.tableData = await this.$get('../../../../static/record0.json')
      },
      handleClick(tab, event) {
          tab.label === '未关联记录' ? this.getrecord0() : this.getrecord1()
      }
    },
    created() {
      this.getrecord1()
    }
  };
</script>


